.about-container {
    width: 90%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 2rem;
    background-color: rgba(255, 255, 255, .4);
    h2 {
        width: 100%;
        text-align: left;
        margin-top: 3rem;
    }
}

@media only screen and (min-width: 992px) {
    .about-container {
        width: 60vw;
    }
    h2 {
        margin: 3rem 0;
    }
}

@media only screen and (min-width: 1440px) {
    .about-container {
        max-width: 40vw;
    }
}